@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,500;1,400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin:0;
  padding:0;
  box-sizing: border-box;
}

html {
  font-size: 16px;
  font-family: 'Poppins', sans-serif;
}


body {
  min-height: 100vh;
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  background-image: url("/WyattTech/reactwebpage/static/media/bg-intro-desktop (1).059183ee.png");
  background-color: hsl(0, 100%, 74%);
  padding: 100px 0;
}

.container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  text-align: center;
  padding: 0 30px;
  max-width: 1300px;
  margin: 0 auto;
}

.left-col {
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: last baseline;
          justify-content: last baseline;
  margin-bottom: 50px;
  margin-top: 250px;
}

.left-col h1 {
  color:#ffffff;
  font-size:32px;
  line-height: 48px;
  margin-bottom: 15px;
}

.left-col p {
  color: #ffffff;
  font-size: 16px;
  line-height: 17px;
}

.right-col {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  -webkit-align-content: flex-end;
          align-content: flex-end;
  margin: 35px;
  padding: 10px;
}

.seven  {
  text-align: center;
  -webkit-justify-content: center;
          justify-content: center;
  border: none;
  outline: none;
  border-radius: 10px;
  padding-top: 15px;
  padding-bottom: 15px;
  padding-left: 155px;
  padding-right: 155px;
  margin-left: -40px;
  margin-right: -10px;
  letter-spacing: 2px;
  background-color: hsl(248, 65%, 67%);
}

.seven input {
  color: white;
  font-weight: 500;
  font-size: 10px;
}

.seven:hover{
  background-color:  hsl(248, 40%, 74%);
  color: white;
}

.right-col .topBox  p {
 font-size: 16.3px;
 line-height: 16.2px;
}

.right-col .topBox p span {
  font-weight: 700;
}

.topBox{
  margin: -35px;
  padding-bottom: 100px;
  text-align: center;
}

 .form-container {
   margin-top: 50px;
  background-color: #ffffff;
  padding: 30px;
  border-radius: 15px;
  box-shadow: 0 7px hsl(249, 10%, 26%);
}

.field-group {
  position: relative;
  margin-bottom: 30px;
  padding: 8px;
}

.right-col input,
.right-col button[type = submit] {
  width: 100%;
  height: 60px;
  margin-bottom: 5px;
  border-radius: 8px;
  border: 1.3px solid hsl(246, 25%, 77%);
  padding-left: 25px;
  outline: none;
  color: hsl(249, 10%, 26%);
  font-family: 'Poppins';
  font-size: 16.10px;
  font-weight: 600px;
}

.right-col input:focus {
  border: 2px solid hsl(249,10%,25%);
}

.btn {
  text-align: center;
  -webkit-justify-content: center;
          justify-content: center;
  border: none;
  border-radius: 10px;
  padding-top: 15px;
  padding-bottom: 15px;
  padding-left: 125px;
  padding-right: 125px;
  margin: 5px;
  background-color: hsla(154, 59%, 51%);
  color: #ffffff;
  font-weight: 500;
  font-size: 12px;
  text-transform: uppercase;
  letter-spacing: 2px;
}

.btn input {
  text-align: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-self: center;
          align-self: center;
}

.btn:hover{
  background-color: hsl(154,80%, 60%);
  color: white;
}

.right-col .form-footer{
  margin-top: 10px;
  color: hsl(246, 21%, 71%);
}

.right-col .form-footer span {
  color:hsl(0, 100%, 74%);
  font-weight: 700;
}

.field {
  padding: 8px;
  margin: -8px;

}

@media screen and (min-width: 970px) {
  #intro {
      background-image: url("/WyattTech/reactwebpage/static/media/bg-intro-mobile (1).02ce3982.png");
      -webkit-flex-direction: row;
              flex-direction: row;
  }
  .container {
      -webkit-flex-direction: row;
              flex-direction: row;
      
  }
  .left-col{
      text-align: left;
      padding-right: 50px;
  }
  .left-col h1 {
      font-size: 48px;
      line-height: 3.5rem;   
  }
 
  .right-col {
      max-width: 500px;
  }
  .right-col .topBox p {
      font-size: 1.2rem;
  }
  .right-col .topBox {
      padding: 20px;
  }
  .right-col .form-container {
      padding: 40px;
  }
  
  .right-col input {
      font-size: 1.05rem;
  }

  .right-col .form-footer {
      margin-top: 20px;
      font-size: .7em;
  }
     
}

